import React, { useEffect, useState } from 'react';
import './Explore.sass';

export default function Explore() {

    const [active, setActive] = useState(0);
    const areas = ['teyvat', 'mondstadt', 'liyue', 'footer'];

    useEffect(() => {
        window.addEventListener('DOMMouseScroll', (e) => {
            console.log(`scroll with detail: ${e.detail}`);
            
            if(e.detail > 0) {
                // Scroll down
                setActive(prev => prev >= 3 ? 3 : prev + 1);
            } else {
                // Scroll up
                setActive(prev => prev <= 0 ? 0 : prev - 1);
            }
        });

        const body = document.querySelector('body');
        body.classList.add('noScroll');

        return () => {
            body.classList.remove('noScroll');
        }
    }, []);

    useEffect(() => {
        const href = `#${areas[active]}`;
        console.log(`going to ${href} [${active}]`);
        window.location.href = href;
    }, [active]);

    return (
        <main className="explore-page">
            <div className="nav__wrapper">
                <ul className="nav">
                    <li 
                        className={`nav__item ${active == 0 ? 'nav__item--active' : ''}`}
                        onClick={() => {setActive(0)}}
                        ><span>TEYVAT</span></li>
                    <li 
                        className={`nav__item ${active == 1 ? 'nav__item--active' : ''}`}
                        onClick={() => {setActive(1)}}
                        ><span>Mondstadt</span></li>
                    <li 
                        className={`nav__item ${active == 2 ? 'nav__item--active' : ''}`}
                        onClick={() => {setActive(2)}}
                        ><span>Liyue</span></li>
                    <li className="nav__item nav__item--inactive">Coming Soon</li>
                </ul>
            </div>
            <div className="area teyvat" id="teyvat">
                <h1 className="teyvat__title">TEYVAT</h1>
                <img className="teyvat__frill" src="/decorations/double-frill.png" />
                <p className="teyvat__text">You have arrived in Teyvat — a fantasy world where the seven elements flow and converge.
                    <br />In the distant past, the Archons granted mortals unique elemental abilities. With the help of such powers, people formed a bountiful homeland out of the wilderness. However, 500 years ago, the collapse of an ancient civilization turned the universe upside down...
                    <br />Though the calamity the world suffered has ceased, peace has yet to be restored.</p>
                <div className="teyvat__hint__wrapper">
                    <div className="teyvat__hint">
                        <img
                            className="teyvat__hint__arrow"
                            src="/decorations/hint-arrow.png" />
                    </div>
                </div>
            </div>
            <div className="area mondstadt" id="mondstadt">
                <img className="mondstadt__emblem" src="/explore/2-emblem.png" />
                <h1 className="mondstadt__title">Mondstadt</h1>
                <p className="mondstadt__text">A city of freedom that lies in the northeast of Teyvat.
                    <br />From amongst mountains and wide-open plains, carefree breezes carry the scent of dandelions — a gift from the Anemo God, Barbatos — across Cider Lake to Mondstadt, which sits on an island in the middle of the lake.</p>
                <div className="area__hint__wrapper">
                    <div className="area__hint"></div>
                </div>
            </div>
            <div className="area liyue" id="liyue">
                <img className="liyue__emblem" src="/explore/3-emblem.png" />
                <h1 className="liyue__title">Liyue</h1>
                <p className="liyue__text">A bountiful harbor that lies in the east of Teyvat.
                    <br />Mountains stand tall and proud alongside the stone forest, that together with the open plains and lively rivers make up Liyue's bountiful landscape, which shows its unique beauty through each of the four seasons. Just how many gifts from the Geo God lie in wait amongst the rocks of Liyue's mountains?</p>
                <div className="area__hint__wrapper">
                    <div className="area__hint"></div>
                </div>
            </div>
        </main>
    )
}
