import React from 'react';
import './Cities.sass';
import { useHistory } from 'react-router-dom';

export default function Cities() {

    const history = useHistory();

    const goToCity = cityId => {
        history.push('characters/' + cityId);
    }

    return (
        <section className="cities">
            <div className="city" onClick={() => { goToCity(0) }}>
                <div className="city__bg" style={{backgroundImage: `url(/cities/city1.jpg)`}}></div>
                <img className="city__girl" src="/cities/city1-char.png" />
                <div className="city__title">
                    <span>Mondstadt</span>
                </div>
            </div>
            <div className="city" onClick={() => { goToCity(1) }}>
                <div className="city__bg" style={{backgroundImage: `url(/cities/city2.jpg)`}}></div>
                <img className="city__girl" src="/cities/city2-char.png" />
                <div className="city__title">
                    <span>Liyue Harbor</span>
                </div>
            </div>
            <div className="city city--inactive">
                <div className="city__bg" style={{backgroundImage: `url(/cities/city3.jpg)`}}></div>
                <div className="city__title">
                    <span>Coming Soon</span>
                </div>
            </div>
        </section>
    )
}
