import React, { useState, useEffect, useRef } from 'react';
import './Carousel.sass';

export default function Carousel({ images }) {

    const [cycler, setCycler] = useState([0, 1]);
    const previousRef = useRef();
    const visibleRef = useRef();

    const fireAnimation = () => {
        // Hacky css but works
        previousRef.current.classList.remove('carousel__image--previous');
        void previousRef.current.offsetWidth;
        previousRef.current.classList.add('carousel__image--previous');

        visibleRef.current.classList.remove('carousel__image--visible');
        void visibleRef.current.offsetWidth;
        visibleRef.current.classList.add('carousel__image--visible');
    }

    const switchTo = imageIndex => {
        setCycler(prev => [prev[1], imageIndex]);
        fireAnimation();
    }

    useEffect(() => {
        const intervalRef = setInterval(() => {
            setCycler(prev => [prev[1], (prev[1]+1) % images.length]);
            fireAnimation();
        }, 10000);

        return () => {
            clearInterval(intervalRef);
        }
    }, []);

    return (
        <div className="carousel">
            <div className="carousel__images">
                <img 
                    ref={previousRef}
                    className={`carousel__image carousel__image--previous`}
                    src={images[cycler[0]]} />
                <img 
                    ref={visibleRef}
                    className={`carousel__image carousel__image--visible`}
                    src={images[cycler[1]]} />
            </div>
            <div className="carousel__controls">
                {images && images.map((image, index) => (
                    <button 
                        key={index}
                        className={`carousel__selector${index == cycler[1] ? ' carousel__selector--active' : ''}`}
                        onClick={() => switchTo(index)}
                        ></button>
                ))}
            </div>
        </div>
    )
}
