import React, { useEffect, useState } from 'react';
import './Header.sass';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faMusic, faUserCircle } from '@fortawesome/free-solid-svg-icons';
import { Link, useHistory } from 'react-router-dom';

export default function Header() {

    const history = useHistory();

    const [activePath, setActivePath] = useState('');
    const [audio, setAudio] = useState(new Audio('/bgm.mp3'));
    const [audioPlaying, setAudioPlaying] = useState(true);

    useEffect(() => {
        const unregister = history.listen((location) => {
            setActivePath(location.pathname);
        });

        setActivePath(history.location.pathname);

        return unregister;
    }, []);

    useEffect(() => {
        if(audioPlaying) audio.play();
        else audio.pause();
    }, [audioPlaying]);

    const toggleMusic = () => {
        setAudioPlaying(prev => !prev);
    }

    return (
        <header className="header">
            <button 
                className="header__music"
                style={{backgroundImage: `url(/buttons/${audioPlaying ? 'bgm-play.png' : 'bgm-stop.png'})`}}
                onClick={toggleMusic}></button>
            <img src="/header-logo.png" className="header__logo" />
            <nav className="header__nav">
                <Link 
                    to="/" 
                    className={`header__link ${activePath == '/' || activePath == '/home' ? 'header__link--active' : ''}`}
                    >Home</Link>
                <Link 
                    to="/characters" 
                    className={`header__link ${activePath.includes('/characters') ? 'header__link--active' : ''}`}
                    >Characters</Link>
                <Link to="/explore" 
                    className={`header__link ${activePath.includes('/explore') ? 'header__link--active' : ''}`}
                    >Explore</Link>
            </nav>
            <div className="header__login">
                <span>Log In</span>
                <FontAwesomeIcon icon={faUserCircle} className="header__login-icon" />
            </div>
        </header>
    )
}
