import React, { useState, useEffect, useRef } from 'react';
import './Characters.sass';
import { useParams } from 'react-router-dom';
const cities =  require('../../assets/cities.json');

export default function Characters() {

    const { cityId } = useParams();

    const [activeCity, setActiveCity] = useState(cities[0]);
    const [activeBg, setActiveBg] = useState(cities[0].bgs[1]);
    const [prevBg, setPrevBg] = useState(cities[0].bgs[0]);
    const [activeCharacter, setActiveCharacter] = useState(cities[0].characters[0]);
    const [audio, setAudio] = useState(new Audio(cities[0].characters[0].voiceSample));
    const [isVoicePlaying, setIsVoicePlaying] = useState(false);
    const [isSwitching, setIsSwitching] = useState(false);
    const [timeoutTrigger, setTimeoutTrigger] = useState(0);

    const spriteRef = useRef();
    const emblemRef = useRef();
    const nameRef = useRef();
    const cvRef = useRef();
    const descriptionRef = useRef();
    const sloganRef = useRef();
    const bgRef = useRef();
    const prevBgRef = useRef();

    const citySwitchTimeoutRef = useRef();
    const charSwitchTimeoutRef = useRef();

    useEffect(() => {
        const endListener = audio.addEventListener('ended', () => { setIsVoicePlaying(false) });

        // BG switcher
        const interval = setInterval(() => {
            setTimeoutTrigger(prev => (prev + 1) % 10);
        }, 7000);

        return () => {
            audio.removeEventListener('ended', endListener);
            clearInterval(interval);
        }
    }, []);

    useEffect(() => {
        // Check if city id passed
        console.log(cityId);
        if(cityId) switchToCity(cityId);
    }, [cityId]);

    useEffect(() => {
        switchToBg(activeCity.bgs[(activeCity.bgs.indexOf(prevBg) + 1) % activeCity.bgs.length ]);
    }, [timeoutTrigger]);

    const fireEnterAnimation = () => {
        // Sprite Animation
        spriteRef.current.classList.remove('swipe-fade-in');
        spriteRef.current.classList.remove('swipe-fade-out');
        void spriteRef.current.offsetWidth;
        spriteRef.current.classList.add('swipe-fade-in');
        // Emblem Animation
        emblemRef.current.classList.remove('fade-in-out');
        emblemRef.current.classList.remove('emblem-fade-out');
        void emblemRef.current.offsetWidth;
        emblemRef.current.classList.add('fade-in-out');
        // Name Animation
        nameRef.current.classList.remove('fade-in');
        nameRef.current.classList.remove('fade-out');
        void nameRef.current.offsetWidth;
        nameRef.current.classList.add('fade-in');
        // CV Animation
        cvRef.current.classList.remove('fade-in');
        cvRef.current.classList.remove('fade-out');
        void cvRef.current.offsetWidth;
        cvRef.current.classList.add('fade-in');
        // Description Animation
        descriptionRef.current.classList.remove('fade-in');
        descriptionRef.current.classList.remove('fade-out');
        void descriptionRef.current.offsetWidth;
        descriptionRef.current.classList.add('fade-in');
        // Slogan Animation
        sloganRef.current.classList.remove('fade-out');
        sloganRef.current.classList.remove('fade-in');
        void sloganRef.current.offsetWidth;
        sloganRef.current.classList.add('fade-in');
    }
    const fireExitAnimation = () => {
        // Sprite Animation
        spriteRef.current.classList.remove('swipe-fade-in');
        spriteRef.current.classList.remove('swipe-fade-out');
        void spriteRef.current.offsetWidth;
        spriteRef.current.classList.add('swipe-fade-out');
        // Emblem Animation
        emblemRef.current.classList.remove('fade-in-out');
        emblemRef.current.classList.remove('emblem-fade-out');
        void emblemRef.current.offsetWidth;
        emblemRef.current.classList.add('emblem-fade-out');
        // Name Animation
        nameRef.current.classList.remove('fade-in');
        nameRef.current.classList.remove('fade-out');
        void nameRef.current.offsetWidth;
        nameRef.current.classList.add('fade-out');
        // CV Animation
        cvRef.current.classList.remove('fade-in');
        cvRef.current.classList.remove('fade-out');
        void cvRef.current.offsetWidth;
        cvRef.current.classList.add('fade-out');
        // Description Animation
        descriptionRef.current.classList.remove('fade-in');
        descriptionRef.current.classList.remove('fade-out');
        void descriptionRef.current.offsetWidth;
        descriptionRef.current.classList.add('fade-out');
        // Slogan Animation
        sloganRef.current.classList.remove('fade-in');
        sloganRef.current.classList.remove('fade-out');
        void sloganRef.current.offsetWidth;
        sloganRef.current.classList.add('fade-out');
    }

    const playCvSample = () => {
        if(isVoicePlaying){
            setIsVoicePlaying(false);
            audio.pause();
        } else {
            setIsVoicePlaying(true);
            audio.src = activeCharacter.voiceSample;
            audio.play();
        }
    }

    const switchToBg = (bg) => {
        setPrevBg(prev => activeBg);
        setActiveBg(prev => bg);
        prevBgRef.current.classList.remove('fade-out');
        void prevBgRef.current.offsetWidth;
        prevBgRef.current.classList.add('fade-out');
    }

    const switchToCity = cityId => {
        const city = cities.filter(c => c.id == cityId)[0];
        setActiveCity(city);
        switchToBg(city.bgs[0]);

        if(citySwitchTimeoutRef.current) clearTimeout(citySwitchTimeoutRef.current);
        
        fireExitAnimation();
        citySwitchTimeoutRef.current = setTimeout(() => {
            setActiveCharacter(city.characters[0]);
            setTimeout(fireEnterAnimation, 200);
        }, 1000);
    }

    const switchToCharacter = characterId => {

        if(charSwitchTimeoutRef.current) clearTimeout(charSwitchTimeoutRef.current);

        fireExitAnimation();
        setIsSwitching(true);
        charSwitchTimeoutRef.current = setTimeout(() => {
            setActiveCharacter(prev => activeCity.characters.filter(c => c.id == characterId)[0]);
            setTimeout(fireEnterAnimation, 200);
            setIsSwitching(false);
        }, 1000);
    }

    const nextCharacter = () => {
        switchToCharacter((activeCharacter.id + 1) % activeCity.characters.length);
    }
    const prevCharacter = () => {
        switchToCharacter((activeCharacter.id + activeCity.characters.length - 1) % activeCity.characters.length);
    }

    return (
        <main className="characters-page" style={{backgroundImage: `url(${activeBg})`}}>
            <img
                ref={bgRef}
                className="bg"
                src={activeBg} />
            <img
                ref={prevBgRef}
                className="bg bg--prev"
                src={prevBg} />
            <img
                ref={spriteRef}
                className="sprite swipe-fade-in"
                src={activeCharacter.spriteImg} />
            <img
                className="shade"
                src="/decorations/char-bg-shade.png" />
            <ul className="cities__list">
                {cities && cities.map(city => (
                    <li 
                        key={city.id} 
                        className={`city ${activeCity.id == city.id ? 'city--active' : ''}`}
                        onClick={() => switchToCity(city.id)}
                        >{city.name}</li>
                ))}
                <li 
                    key={100} 
                    className="city"
                    style={{cursor: 'initial'}}
                    >Coming Soon</li>
            </ul>
            <div className="character">
                <img
                    ref={emblemRef}
                    className="character__emblem fade-in-out"
                    src={activeCharacter.emblemImg} />
                <div className="character__dash"></div>
                <img
                    ref={nameRef}
                    className="character__name fade-in"
                    src={activeCharacter.nameImg} />
                <div ref={cvRef} className="character__cv  fade-in">
                    <img className="character__cv__icon" src="/decorations/cv-icon.jpeg" />
                        <div className="character__cv__name">CV: {activeCharacter.cv}</div>
                    <button 
                        className="character__cv__play"
                        style={{backgroundImage: `url('/decorations/${isVoicePlaying ? 'cv-playing.gif' : 'cv-play.png'}')`}}
                        onClick={playCvSample}></button>
                </div>
                <div ref={descriptionRef} className="character__description  fade-in">
                    <div className="character__description__text">
                        {activeCharacter.description.map((d, i) => (
                            <p key={i}>{d}</p>
                        ))}
                    </div>
                </div>
                <img
                    ref={sloganRef}
                    className="character__slogan  fade-in"
                    src={activeCharacter.sloganImg} />
            </div>
            <div class="characters__bg"></div>
            <div className="characters">
                <div class="characters__wrapper">
                    <div className="characters__list">
                        <button 
                            className="characters__slider-left"
                            onClick={prevCharacter}
                            disabled={isSwitching}></button>
                        {activeCity && activeCity.characters.map(character => (
                            <div 
                                className={`characters__item ${character.id == activeCharacter.id ? 'characters__item--active' : ''}`}
                                onClick={() => { if(character.id != activeCharacter.id) switchToCharacter(character.id) }}
                                >
                                <img className="characters__avatar" src={character.avatarImg} />
                                <span className="characters__name">{character.name}</span>
                            </div>
                        ))}
                        <button 
                            className="characters__slider-right"
                            onClick={nextCharacter}
                            disabled={isSwitching}></button>
                    </div>
                </div>
            </div>
        </main>
    )
}